import React, { useState, useEffect, useCallback } from "react"
import moment from "moment"
import { supabase } from "../supabaseClient" // Supabase  
import { useToast } from "../contexts/ToastContext"
import { useConfirm } from "../contexts/ConfirmContext"
import DatePicker from "react-datepicker"
import "react-datepicker/dist/react-datepicker.css"

const DailyRecordModal = ({ selectedDate, isOpen, onClose, session, jobs, recordToEdit }) => {
	const showToast = useToast()
	const showConfirm = useConfirm()
	const [startTime, setStartTime] = useState("")
	const [endTime, setEndTime] = useState("")
	const [mealAllowance, setMealAllowance] = useState(0)
	const [notes, setNotes] = useState("")
	const [dailyWage, setDailyWage] = useState(0)
	const [recordId, setRecordId] = useState(null)
	const [hourlyRateForDate, setHourlyRateForDate] = useState(0)
	const [timeError, setTimeError] = useState(false)
	const [selectedJobId, setSelectedJobId] = useState(null) //   ID 

	const [showModal, setShowModal] = useState(false) //    
	const [animateModal, setAnimateModal] = useState(false) //    

	const resetForm = useCallback(() => {
		setStartTime("")
		setEndTime("")
		setMealAllowance(0)
		setNotes("")
		setDailyWage(0)
		setRecordId(null)
		setTimeError(false)
		if (jobs.length > 0) {
			setSelectedJobId(jobs[0].id) //      
		}
	}, [jobs])

	useEffect(() => {
		if (isOpen) {
			setShowModal(true) //  DOM  
			setTimeout(() => setAnimateModal(true), 10) //     
			document.body.classList.add("modal-open") //    body  
			if (recordToEdit) {
				setRecordId(recordToEdit.id)
				setSelectedJobId(recordToEdit.job_id)
				setStartTime(recordToEdit.start_time)
				setEndTime(recordToEdit.end_time)
				setMealAllowance(recordToEdit.meal_allowance)
				setNotes(recordToEdit.notes)
				setDailyWage(recordToEdit.daily_wage)
			} else {
				resetForm()
			}
		} else {
			setAnimateModal(false) //   
			setTimeout(() => setShowModal(false), 300) //    DOM  (300ms transition-duration )
			document.body.classList.remove("modal-open") //    body   
			resetForm()
		}
	}, [isOpen, recordToEdit, resetForm])

	//     (   )
	useEffect(() => {
		return () => {
			document.body.classList.remove("modal-open")
		}
	}, [])

	//           useEffect
	useEffect(() => {
		const fetchHourlyRateForDate = async () => {
			if (!session || !selectedJobId || !selectedDate) {
				setHourlyRateForDate(0)
				return
			}

			const { data, error } = await supabase
				.from("hourly_rate_history")
				.select("hourly_rate")
				.eq("user_id", session.user.id)
				.eq("job_id", selectedJobId)
				.lte("effective_date", moment(selectedDate).format("YYYY-MM-DD"))
				.or(`end_date.gte.${moment(selectedDate).format("YYYY-MM-DD")},end_date.is.null`)
				.order("effective_date", { ascending: false })
				.limit(1)

			if (error) {
				console.error("Error fetching hourly rate for date:", error)
				setHourlyRateForDate(0)
			} else if (data && data.length > 0) {
				setHourlyRateForDate(data[0].hourly_rate)
			} else {
				setHourlyRateForDate(0)
			}
		}

		fetchHourlyRateForDate()
	}, [session, selectedJobId, selectedDate])

	//        useEffect
	useEffect(() => {
		if (startTime && endTime && hourlyRateForDate !== 0) {
			const startMoment = moment(startTime, "HH:mm")
			const endMoment = moment(endTime, "HH:mm")

			if (endMoment.isBefore(startMoment)) {
				setTimeError(true)
				setDailyWage(0)
				return
			}
			setTimeError(false)

			const duration = moment.duration(endMoment.diff(startMoment))
			const hours = duration.asHours()
			const calculatedWage = Math.round(hours * hourlyRateForDate) + mealAllowance
			setDailyWage(calculatedWage)
		} else {
			setDailyWage(0)
		}
	}, [startTime, endTime, hourlyRateForDate, mealAllowance])

	const handleSave = async () => {
		if (!session || !selectedJobId || !selectedDate || !startTime || !endTime) {
			showToast(" .", "error")
			return
		}

		//   
		const startMoment = moment(startTime, "HH:mm")
		const endMoment = moment(endTime, "HH:mm")

		if (endMoment.isBefore(startMoment)) {
			showToast("   .", "error")
			return
		}

		//   (hourlyRateForDate )
		const duration = moment.duration(endMoment.diff(startMoment))
		const hours = duration.asHours()
		const calculatedDailyWage = Math.round(hours * hourlyRateForDate) + mealAllowance

		const newRecord = {
			user_id: session.user.id,
			job_id: selectedJobId,
			date: moment(selectedDate).format("YYYY-MM-DD"),
			start_time: startTime,
			end_time: endTime,
			meal_allowance: mealAllowance,
			notes: notes,
			daily_wage: calculatedDailyWage,
		}

		try {
			if (recordId) {
				//   
				const { error } = await supabase.from("work_records").update(newRecord).eq("id", recordId)
				if (error) throw error
				showToast("", "success")
			} else {
				//   
				const { error } = await supabase.from("work_records").insert([newRecord])
				if (error) throw error
				showToast("", "success")
			}
			onClose()
			resetForm()
		} catch (error) {
			console.error("   :", error)
			showToast(" ", "error")
		}
	}

	const handleDelete = async () => {
		if (!recordId) {
			showToast("  ", "error")
			return
		}

		showConfirm(" ?", async () => {
			try {
				const { error } = await supabase.from("work_records").delete().eq("id", recordId)

				if (error) {
					throw error
				}

				showToast("", "success")
				onClose() //  
				resetForm() //  
			} catch (error) {
				console.error("   :", error)
				showToast(" ", "error")
			}
		})
	}

	if (!showModal) return null

	return (
		<div className={`fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50 transition-opacity duration-300 ease-out ${animateModal ? "opacity-100 pointer-events-auto" : "opacity-0 pointer-events-none"}`}>
			<div className={`bg-cream-white dark:bg-charcoal-gray rounded-2xl shadow-lg p-6 w-full max-w-xs px-4 max-h-[90vh] overflow-y-auto transform transition-all duration-300 ease-out ${animateModal ? "translate-y-0" : "translate-y-10"}`}>
				<div className="flex justify-between items-center mb-4">
					<h2 className="text-xl font-bold text-dark-navy dark:text-white">{moment(selectedDate).format("YYYY M D (ddd)")} </h2>
					<button onClick={onClose} className="text-medium-gray dark:text-light-gray hover:text-dark-navy dark:hover:text-white text-2xl transition-all duration-200 ease-in-out transform hover:scale-105">
						&times;
					</button>
				</div>

				<div className="space-y-4">
					<div>
						<label htmlFor="jobSelect" className="block text-sm font-medium text-medium-gray dark:text-light-gray">
							 
						</label>
						<select
							id="jobSelect"
							value={selectedJobId || ""}
							onChange={(e) => setSelectedJobId(e.target.value)}
							className="mt-1 block w-full px-3 py-2 border border-gray-300 rounded-md shadow-md focus:outline-none focus:ring-mint-green focus:border-mint-green sm:text-sm bg-cream-white dark:bg-charcoal-gray text-dark-navy dark:text-white">
							{jobs.length === 0 && <option value=""> </option>}
							{jobs.map((job) => (
								<option key={job.id} value={job.id}>
									{job.job_name}
								</option>
							))}
						</select>
					</div>
					<div>
						<label htmlFor="hourlyRateDisplay" className="block text-sm font-medium text-medium-gray dark:text-light-gray">
							 
						</label>
						<input
							type="text"
							id="hourlyRateDisplay"
							value={`${hourlyRateForDate != null ? hourlyRateForDate.toLocaleString() : "0"}`}
							readOnly
							className="mt-1 block w-full px-3 py-2 border border-gray-300 rounded-md shadow-md 
     bg-gray-100 dark:bg-gray-700 text-dark-navy dark:text-white sm:text-sm"
						/>
					</div>
					<div>
						<label htmlFor="startTime" className="block text-sm font-medium text-medium-gray dark:text-light-gray">
							 
						</label>
						<DatePicker
							id="startTime"
							selected={startTime ? moment(startTime, "HH:mm").toDate() : null}
							onChange={(date) => setStartTime(moment(date).format("HH:mm"))}
							showTimeSelect
							showTimeSelectOnly
							timeIntervals={15}
							dateFormat="HH:mm"
							timeFormat="HH:mm"
							className="mt-1 block w-full px-3 py-2 border border-gray-300 rounded-md shadow-md focus:outline-none focus:ring-mint-green focus:border-mint-green sm:text-sm bg-cream-white dark:bg-charcoal-gray text-dark-navy dark:text-white"
						/>
					</div>
					<div>
						<label htmlFor="endTime" className="block text-sm font-medium text-medium-gray dark:text-light-gray">
							 
						</label>
						<DatePicker
							id="endTime"
							selected={endTime ? moment(endTime, "HH:mm").toDate() : null}
							onChange={(date) => setEndTime(moment(date).format("HH:mm"))}
							showTimeSelect
							showTimeSelectOnly
							timeIntervals={15}
							dateFormat="HH:mm"
							timeFormat="HH:mm"
							className="mt-1 block w-full px-3 py-2 border border-gray-300 rounded-md shadow-md focus:outline-none focus:ring-mint-green focus:border-mint-green sm:text-sm bg-cream-white dark:bg-charcoal-gray text-dark-navy dark:text-white"
						/>
						{timeError && <p className="text-coral-pink text-sm mt-1">     .</p>}
					</div>
					<div>
						<label htmlFor="mealAllowance" className="block text-sm font-medium text-medium-gray dark:text-light-gray">
							 ()
						</label>
						<input
							type="number"
							id="mealAllowance"
							value={mealAllowance}
							onChange={(e) => setMealAllowance(Math.max(0, parseInt(e.target.value) || 0))}
							className="mt-1 block w-full px-3 py-2 border border-gray-300 rounded-md shadow-md focus:outline-none focus:ring-mint-green focus:border-mint-green sm:text-sm bg-cream-white dark:bg-charcoal-gray text-dark-navy dark:text-white"
						/>
					</div>
					<div>
						<label htmlFor="notes" className="block text-sm font-medium text-medium-gray dark:text-light-gray">
							
						</label>
						<textarea
							id="notes"
							rows="2"
							value={notes}
							onChange={(e) => setNotes(e.target.value)}
							className="mt-1 block w-full px-3 py-2 border border-gray-300 rounded-md shadow-md focus:outline-none focus:ring-mint-green focus:border-mint-green sm:text-sm bg-cream-white dark:bg-charcoal-gray text-dark-navy dark:text-white"></textarea>
					</div>
				</div>

				<div className="mt-6 text-right text-lg font-semibold text-mint-green"> : {dailyWage.toLocaleString()}</div>

				<div className="mt-6 flex flex-col-reverse gap-3 sm:flex-row sm:justify-between sm:items-center">
					{recordId && (
						<button onClick={handleDelete} className="w-full sm:w-auto px-4 py-2 text-coral-pink bg-coral-pink-light rounded-lg hover:bg-coral-pink focus:outline-none focus:ring-2 focus:ring-coral-pink focus:ring-opacity-50 transition-all duration-200 ease-in-out transform hover:scale-105">
							
						</button>
					)}
					<div className="flex flex-col gap-3 sm:flex-row sm:ml-auto">
						<button
							onClick={onClose}
							className="w-full sm:w-auto px-4 py-2 bg-medium-gray text-white rounded-lg hover:bg-gray-600 focus:outline-none focus:ring-2 focus:ring-medium-gray focus:ring-opacity-50 transition-all duration-200 ease-in-out
     transform hover:scale-105">
							
						</button>
						<button
							onClick={resetForm}
							className="w-full sm:w-auto px-3 py-2 bg-yellow-500 text-white rounded-lg hover:bg-yellow-600 focus:outline-none focus:ring-2 focus:ring-yellow-500 focus:ring-opacity-50 transition-all duration-200 
     ease-in-out transform hover:scale-105">
							
						</button>
						<button
							onClick={handleSave}
							className="w-full sm:w-auto px-4 py-2 bg-mint-green text-white rounded-lg hover:bg-mint-green-dark focus:outline-none focus:ring-2 focus:ring-mint-green focus:ring-opacity-50 transition-all duration-200 
     ease-in-out transform hover:scale-105">
							
						</button>
					</div>
				</div>
			</div>
		</div>
	)
}

export default DailyRecordModal
